import { DatePipe } from '@angular/common';
import { Component, Input } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';
import { SharedService } from '../../../../../../services/shared.service';
import { ReturnStatus } from '../../../../../../shared/enums/enum';
import { PaymentModalComponent } from '../../../collect/payment-modal/payment-modal.component';
import { CollectService } from '../../../collect/services/collect.service';
import { ModalPopupComponent } from '../modal-popup/modal-popup.component';
import { RefundDetailsComponent } from '../refund-details/refund-details.component';
import { SignComponent } from '../sign/sign.component';
import { TableColumn, TableConfig } from '../../../../../../shared/components/table-advanced/table-advanced-interfaces';
import { FormBuilder, FormGroup } from '@angular/forms';
import { debounceTime, distinctUntilChanged } from 'rxjs';

@Component({
  selector: 'app-direct-payment-return',
  templateUrl: './direct-payment-return.component.html',
  styleUrls: ['./direct-payment-return.component.scss']
})
export class DirectPaymentReturnComponent {
  @Input() data;
  pageNo = 1;
  total = 0;
  limit = 6;
  loading = false;
  tableConfig: TableConfig = {
    paging: true,
    filter: {
      Sort: 1,
      PageSize: this.limit,
    },
  };
  tableColumns: TableColumn[] = [];
  returnStatus = ReturnStatus;
  returnType = '';
  dateFilter = null;
  returnOrdersList = [];
  branches = [];
  users = [];
  registerDetails;
  isRegisterBranch;
  orderStatus = null;
  statusList = [];
  filterForm: FormGroup
  filterParams;
  constructor(
    private _collectService: CollectService,
    private _commonService: CommonService,
    private _sharedService: SharedService,
    private fb: FormBuilder,
    private _datePipe: DatePipe,
    private _modalService: NgbModal) {
  }
  ngOnInit() {
    //this.getDpReturnOrders();
    this.getRegisterDetails();
    this.getUsers();
    this.getStausList();
    this.initFilterParams();
    this.initTableColumns();
  }
  initFilterParams() {
    this.filterForm = this.fb.group({
      branchId: [null],
      customer: [''],
      created: [null],
      orderNo: [''],
      sentTo: [''],
      date: [''],
      status: [null],
    })
    let returnParam = JSON.parse(localStorage.getItem('returnParams'));
    if (returnParam?.branchId) {
      if (returnParam?.date) {
        let date = [new Date(returnParam?.date[0]), new Date(returnParam?.date[1])];
        returnParam = { ...returnParam, date };
      }
      this.filterForm.patchValue(returnParam);
    }
    this.filterForm.valueChanges.pipe(debounceTime(500), distinctUntilChanged()).subscribe(value => {
      this.pageNo = 1;
      let dateObj = { returnDateFrom: '', returnDateTo: '' };
      let formValues;
      if (value['date']) {
        dateObj['returnDateFrom'] = this._datePipe.transform(value['date'][0], 'yyyy-MM-dd');
        dateObj['returnDateTo'] = this._datePipe.transform(value['date'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...dateObj };
      delete formValues['date'];
      if (formValues.branchId == null) {
        formValues = { ...formValues, branchId: '' }
      }
      if (formValues.status == null) {
        formValues = { ...formValues, status: '' }
      }
      if (formValues.created == null) {
        formValues = { ...formValues, created: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      this.getDpReturnOrders()
    })
  }
  getDpReturnOrders() {

    let defaultParams = `pageSize=${this.tableConfig.filter.PageSize}&pageNo=${this.pageNo - 1}&sort=${this.tableConfig.filter.Sort}`;
    let params;
    if (this.filterParams) params = `${this.filterParams}&${defaultParams}`; else params = `?${defaultParams}`;
    this._collectService.getDpReturnOrders(params).subscribe((response: any) => {
      this.returnOrdersList = response?.data;
      this.total = response?.totalRecordCount;
    })
  }
  getStausList() {
    this._collectService.getLookupsById(10).subscribe((response: any) => {
      this.statusList = response.data.map(x => {
        return {
          text: x?.name[0]?.lookupName,
          value: x?.id
        }
      });
    })
  }
  eventData(event) {
    if (event.isTableRowClicked) {
      let id = event.eventData.target.id;
      if (id === 'orderAction') {
        return;
      }
      else {
        let obj = { ...this.filterForm.value };
        localStorage.setItem('returnParams', JSON.stringify({ ...JSON.parse(localStorage.getItem('returnParams')), ...obj }));
        this._commonService.NavigateToRoute(`/return/dpayment-details/${event?.item?.directPaymentSalesOrderOpportunityNo}`);
      }
    }
  }
  downloadInvoices(invoices) {
    this._commonService.downloadInvoices({ invoices })
  }
  getRegisterDetails() {
    this._collectService.GetRegisterDetails(this._sharedService.getRegister.registerObj?.id).subscribe((response: any) => {
      this.registerDetails = response.data;
      if (this.registerDetails?.collectForOwnBranch) {
        this.f.get('branchId').disable();
      }
      this.getBranches(3001);
    })
  }
  get f() {
    return this.filterForm;
  }
  getBranches(branchTypeId?) {
    this.branches = this.registerDetails.registerBranches.map(x => {
      return { ...x, branchType: x?.branchTypes[0]?.branchTypeId, }
    }).filter(branch => branch.branchType == branchTypeId);
    this.isRegisterBranch = this.registerDetails.branchId;
    if (!this.registerDetails?.collectForOwnBranch) {
      this.filterForm.get('branchId').setValue(this.branches[0].branchId);
    }
    else {
      let index = this.branches.findIndex(branch => branch.branchId == this.isRegisterBranch);
      if (index >= 0) {
        this.filterForm.get('branchId').setValue(this.isRegisterBranch);
      }
      else this.filterForm.get('branchId').setValue(null);
    }
  }
  getUsers() {
    let roleId = 3;
    return this._collectService
      .getUsers(roleId)
      .subscribe((response: any) => {
        if (response) {
          this.users = response.data;
        }
      });
  }
  returnAction(type, details) {
    let action = details?.actionObj[0]?.lookupName.toLowerCase();
    if (action === 'view') {
      const modalRef = this._modalService.open(RefundDetailsComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details, orderType: 7002 };
    }
    else if (action === 'collect') {
      const modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
      modalRef.componentInstance.detailsItem = { ...details, isFromReturn: true };
      modalRef.componentInstance.collectionType = 7002;
      modalRef.componentInstance.sendtoLoadData.subscribe(x => {
        modalRef.dismiss();
        this.getDpReturnOrders();
      });
    }
    else if (action === 'sign') {
      const modalRef = this._modalService.open(SignComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details, orderType: 7002 };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getDpReturnOrders();
        }
      });
    }
    else if (action === 'refund') {
      const modalRef = this._modalService.open(ModalPopupComponent, { size: 'lg' });
      modalRef.componentInstance.data = { returnType: this.returnType, details, orderType: 7002 };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getDpReturnOrders();
        }
      });
    }
  }
  onSortChange(sort: any) {
    if (sort?.direction && sort?.column) {
      switch (sort.column) {
        case 'directPaymentSalesOrderOpportunityNo':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 3 : 2;
          break;
        case 'customer.customerName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 5 : 4;
          break;
        case 'returnedBy.fullName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 7 : 6;
          break;
        case 'returnAt':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 9 : 8;
          break;
        case 'grandAmount':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 11 : 10;
          break;
        case 'statusObj[0].lookupName':
          this.tableConfig.filter.Sort = sort.direction === 'desc' ? 13 : 12;
          break;
        default:
          break;
      }
    } else {
      this.tableConfig.filter.Sort = 1;
    }
    this.getDpReturnOrders();
  }
  onPageChange(page: number) {
    this.pageNo = page;
    this.getDpReturnOrders();
  }
  initTableColumns() {
    this.tableColumns = [
      { key: 'directPaymentSalesOrderOpportunityNo', label: 'Order No', canSort: true, },
      { key: 'customer.customerName', label: 'Customer Details', canSort: true, },
      { key: 'returnedBy.fullName', label: 'Return By', canSort: true, },
      { key: 'returnAt', label: 'Return Date', canSort: true, dateFormat: 'mediumDate' },
      { key: 'grandAmount', label: 'Refund/Collect Amount', canSort: true, currency: { decimalFormat: '2.3-3', appendText: ' JOD' } },
      { key: 'statusObj[0].lookupName', label: 'Status', canSort: true, },
      { key: 'action', label: 'Action', },
    ];
  }
}
