import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { setTime } from 'ngx-bootstrap/chronos/utils/date-setters';
import { DownloadInvoicesComponent } from '../../../../../../shared/components/download-invoices/download-invoices.component';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';
import { CollectService } from '../../../collect/services/collect.service';

@Component({
  selector: 'app-collect',
  templateUrl: './collect.component.html',
  styleUrls: ['./collect.component.scss']
})
export class CollectComponent {
  @Input() data;
  @Output() eventData = new EventEmitter<any>();
  message = '';
  constructor(public _activeModal: NgbActiveModal,
    private _modalService: NgbModal,
    private _collectService: CollectService,
  ) { }
  ngOnInit() {
    console.log(this.data);
  }
  handleConfirm() {
    const formData = new FormData();
    formData.append('OrderId', this.data?.details?.sparePartsSalesOrderOpportunityNo);
    this._collectService.collectZeroReturnOrder(formData).subscribe(response => {
      console.log(response);
      if (response.isSuccess) {
        this.eventData.emit({ isSuccess: response.isSuccess });
        const modalRef = this._modalService.open(DownloadInvoicesComponent)
        modalRef.componentInstance.data = { message: 'Successfully done!', invoices: response.data};
      }
    }, error => {
      this.message = error?.error?.errors[0]?.ErrorMessageEn;
      setTimeout(() => this.message = '', 3000)
    })
  }
}
