import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ReturnComponent } from './return.component';
import { ServicesDetailsComponent } from './services-details/services-details.component';
import { SpartPartDetailsComponent } from './spart-part-details/spart-part-details.component';
import { DirectPaymentDetailsComponent } from './direct-payment-details/direct-payment-details.component';

const routes: Routes = [
  { path: '', component: ReturnComponent },
  { path: 'details', component: SpartPartDetailsComponent },
  { path: 'services-details/:id', component: ServicesDetailsComponent },
  { path: 'dpayment-details/:orderId', component: DirectPaymentDetailsComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReturnRoutingModule { }
