import { DatePipe } from '@angular/common';
import { Component, Input } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { CommonService } from '../../../../../../services/common.service';
import { SharedService } from '../../../../../../services/shared.service';
import { ReturnStatus } from '../../../../../../shared/enums/enum';
import { PaymentModalComponent } from '../../../collect/payment-modal/payment-modal.component';
import { CollectService } from '../../../collect/services/collect.service';
import { CollectComponent } from '../collect/collect.component';
import { ModalPopupComponent } from '../modal-popup/modal-popup.component';
import { RefundDetailsComponent } from '../refund-details/refund-details.component';
import { SignComponent } from '../sign/sign.component';
import { ModalMessageComponent } from '../../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'app-spare-part-return',
  templateUrl: './spare-part-return.component.html',
  styleUrls: ['./spare-part-return.component.scss']
})
export class SparePartReturnComponent {
  returnStatus = ReturnStatus;
  searchOrderNo = '';
  searchCustomerName = '';
  dateObj = { fromDate: '', toDate: '' };
  dateFilter = null;
  returnType = '';
  @Input() data;
  returnOrdersList = [];
  branches = [];
  users = [];
  branchId = null;
  userId = null;
  sort = 1;
  pageNo = 1;
  totalRecords = 0;
  registerDetails;
  isRegisterBranch;
  orderStatus = null;
  statusList = [];
  constructor(
    private _collectService: CollectService,
    private _commonService: CommonService,
    private _datePipe: DatePipe,
    private _sharedService: SharedService,
    private _modalService: NgbModal) {
  }
  ngOnInit() {
    this.getRegisterDetails();
    this.getUsers();
    this.getStausList();
  }
  onSelectFile(event: any, row) {
    if (event.target.files && event.target.files[0]) {
      this._commonService.fileToBase64(event.target.files[0]).then((response: any) => {
        const formData = new FormData();
        formData.append('ReturnOrderId', row?.sparePartsSalesOrderOpportunityNo);
        formData.append('file', response.file || '')
        this._collectService.uploadCreditRefund(formData).subscribe(response => {
          if (response.isSuccess) {
            this.successModal('success', 'Return receipt uploaded successfully!');
            this.getSparePartsReturnOrders();
          }
        }, (err => {
          this.successModal('error', err.error.errors);
        }))
      })
    }
  }
  successModal(type, message) {
    const modelRef = this._modalService.open(ModalMessageComponent, { size: 'md' })
    modelRef.componentInstance.type = type;
    if (type == 'success') {
      modelRef.componentInstance.message = message;
    }
    else if (type == 'error') {
      modelRef.componentInstance.messageError = message;
    }
  }
  filterByOrderStatus(event) {
    if (event) {
      this.orderStatus = event?.value;
      this.pageNo = 1;
      this.getSparePartsReturnOrders();
    }
    else {
      this.pageNo = 1;
      this.orderStatus = null
      this.getSparePartsReturnOrders();
    }
  }
  getStausList() {
    this._collectService.getLookupsById(10).subscribe((response: any) => {
      this.statusList = response.data.map(x => {
        return {
          text: x?.name[0]?.lookupName,
          value: x?.id
        }
      });
    })
  }
  viewDetails(event, id) {
    let target = (event.target as HTMLElement).tagName.toLowerCase();
    let eleId = (event.target as HTMLElement).id;
    let returnReceipt = (event.target as HTMLElement).id;
    if (target == 'button' || eleId == 'elipse' || returnReceipt == 'uploadFile' || returnReceipt == 'returnReceipt') {
      return;
    }
    else {
      let obj = {
        branchId: this.branchId,
        searchCustomerName: this.searchCustomerName,
        searchOrderNo: this.searchOrderNo,
        userId: this.userId,
        orderStatus: this.orderStatus,
        dateFilter: this.dateFilter,
      };
      localStorage.setItem('returnParams', JSON.stringify({ ...JSON.parse(localStorage.getItem('returnParams')), ...obj }));
      this._commonService.NavigateToRouteWithQueryString(`/return/details`, { queryParams: { id } });
    }
  }
  downloadInvoices(invoices) {
    this._commonService.downloadInvoices({ invoices })
  }
  getRegisterDetails() {
    this._collectService.GetRegisterDetails(this._sharedService.getRegister.registerObj?.id).subscribe((response: any) => {
      this.registerDetails = response.data;
      this.getBranches(3003);
    })
  }
  getBranches(branchTypeId?) {
    this.branches = this.registerDetails.registerBranches.map(x => {
      return { ...x, branchType: x?.branchTypes[0]?.branchTypeId, }
    }).filter(branch => branch.branchType == branchTypeId);
    this.isRegisterBranch = this.registerDetails.branchId;
    if (!this.registerDetails?.collectForOwnBranch) {
      this.branchId = this.branches[0].branchId;
    }
    else {
      let index = this.branches.findIndex(branch => branch.branchId == this.isRegisterBranch);
      if (index >= 0) {
        this.branchId = this.isRegisterBranch;
      }
      else this.branchId = null;
    }
    let returnParam = JSON.parse(localStorage.getItem('returnParams'));
    if (returnParam?.branchId) {
      this.branchId = returnParam.branchId;
      this.searchCustomerName = returnParam.searchCustomerName;
      this.searchOrderNo = returnParam.searchOrderNo;
      this.userId = returnParam.userId;
      this.orderStatus = returnParam.orderStatus;
      if (returnParam?.dateFilter) {
        this.dateFilter = [new Date(returnParam?.dateFilter[0]), new Date(returnParam?.dateFilter[1])];
      }
    }
    if (this.branchId !== null) {
      this.getSparePartsReturnOrders();
    }
  }
  getUsers() {
    let roleId;
    if (this.data['branchTypeId'] == 3003) {
      roleId = 33
    } else if (this.data['branchTypeId'] == 3002) {
      roleId = 32
    }
    return this._collectService
      .getUsers(roleId)
      .subscribe((response: any) => {
        if (response) {
          this.users = response.data;
        }
      });
  }
  //Filters Start
  handleBranchChange(event) {
    let brandId = event?.branchId;
    if (brandId !== null || brandId !== undefined) {
      this.branchId = event?.branchId;
      this.pageNo = 1;
      this.getSparePartsReturnOrders();
    }
  }
  clearBranch(event) {
    this.branchId = event?.branchId;
    this.returnOrdersList = [];
    this.totalRecords = 0;
  }
  filterByUserId(event) {
    this.pageNo = 1;
    this.userId = event?.userId;
    this.getSparePartsReturnOrders();
  }
  clearUser() {
    this.pageNo = 1;
    this.getSparePartsReturnOrders();
  }
  handleSearchByOrderNo(event) {
    this.pageNo = 1;
    this.searchOrderNo = event?.target.value;
    this.getSparePartsReturnOrders();
  }
  clearSearchByOrderNo() {
    this.pageNo = 1;
    this.searchOrderNo = '';
    this.getSparePartsReturnOrders();
  }
  handleSearchByName(event) {
    this.pageNo = 1;
    this.searchCustomerName = event?.target.value;
    this.getSparePartsReturnOrders();
  }
  clearSearchByName() {
    this.pageNo = 1;
    this.searchCustomerName = '';
    this.getSparePartsReturnOrders();
  }
  handleSearchByDate(event) {
    if (event) {
      this.dateObj['fromDate'] = this._datePipe.transform(event[0], 'yyyy-MM-dd');
      this.dateObj['toDate'] = this._datePipe.transform(event[1], 'yyyy-MM-dd');;
    }
    this.pageNo = 1;
    this.getSparePartsReturnOrders();
  }
  clearSearchByDate() {
    this.pageNo = 1;
    this.dateObj = { fromDate: '', toDate: '' };
    this.dateFilter = null;
    this.getSparePartsReturnOrders();
  }
  //Filters End
  getSparePartsReturnOrders() {
    let defaultParams = `status=${this.orderStatus || ''}&customer=${this.searchCustomerName}&ReturnDateFrom=${this.dateObj?.fromDate}&ReturnDateTo=${this.dateObj?.toDate}&sort=${this.sort}&orderNo=${this.searchOrderNo}
    &pageNo=${this.pageNo - 1}&sort=${this.sort}&pageSize=${6}`
    if (this.userId) {
      defaultParams += `&created=${this.userId}`
    }
    if (+(this.branchId == 0) || +(this.branchId) > 0) {
      defaultParams += `&branchId=${this.branchId}`
    }
    this._collectService.getSparePartsReturnOrders(defaultParams).subscribe((response: any) => {
      this.returnOrdersList = response?.data;
      this.totalRecords = response?.totalRecordCount;
    })
  }
  returnAction(type, details) {
    this.returnType = type.toLowerCase();
    let action = details?.actionObj[0]?.lookupName.toLowerCase();
    if (action === 'view') {
      const modalRef = this._modalService.open(RefundDetailsComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details, orderType: 7003};
    }
    else if (action === 'collect') {
      if (details?.grandAmount > 0) {
        const modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
        modalRef.componentInstance.detailsItem = { ...details, isFromReturn: true };
        modalRef.componentInstance.collectionType = 7003;
        modalRef.componentInstance.sendtoLoadData.subscribe(x => {
          modalRef.dismiss();
          this.getSparePartsReturnOrders();
        });
      }
      else {
        const modalRef = this._modalService.open(CollectComponent, { size: 'lg' });
        modalRef.componentInstance.data = { details };
        modalRef.componentInstance.eventData.subscribe(x => {
          if (x?.isSuccess) {
            modalRef.dismiss();
            this.getSparePartsReturnOrders();
          }
        });
      }
    }
    else if (action === 'sign') {
      const modalRef = this._modalService.open(SignComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getSparePartsReturnOrders();
        }
      });
    }
    else if (action === 'refund') {
      const modalRef = this._modalService.open(ModalPopupComponent, { size: 'lg' });
      modalRef.componentInstance.data = { returnType: this.returnType, details, orderType: 7003 };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getSparePartsReturnOrders();
        }
      });
    }
  }
  pageChange(pageNo) {
    this.pageNo = pageNo;
    this.getSparePartsReturnOrders();
  }
  // Sorting Functions
  sortByOrderNo() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getSparePartsReturnOrders();
  }
  sortByCustomerDetails() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getSparePartsReturnOrders();

  }
  sortByReturn() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getSparePartsReturnOrders();

  }
  sortByReturnDate() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getSparePartsReturnOrders();

  }
  sortByRefund() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getSparePartsReturnOrders();

  }
  sortByStatus() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getSparePartsReturnOrders();

  }

}
