import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';
import { SharedService } from '../../../../../services/shared.service';
import { SubjectService } from '../../../../../services/subject.service';
import { CollectService } from '../../collect/services/collect.service';
import { RefundDetailsComponent } from '../components/refund-details/refund-details.component';
import { PaymentModalComponent } from '../../collect/payment-modal/payment-modal.component';
import { ModalPopupComponent } from '../components/modal-popup/modal-popup.component';
import { SignComponent } from '../components/sign/sign.component';
import { RejectRefundComponent } from '../components/reject-refund/reject-refund.component';
import { CommonService } from '../../../../../services/common.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-direct-payment-details',
  templateUrl: './direct-payment-details.component.html',
  styleUrls: ['./direct-payment-details.component.scss']
})
export class DirectPaymentDetailsComponent {
  details: any;
  custmerName: string;
  orderStatus = '';
  orderId = 0
  constructor(
    private collectService: CollectService,
    private route: ActivatedRoute,
    public sharedService: SharedService,
    private _modalService: NgbModal,
    private _commonService: CommonService,
    private _location: Location,
  ) { }
  ngOnInit(): void {
    this.route.params.subscribe((x: any) => {
      this.orderId = x.orderId;
      this.GetDirectPaymentSalesOrdersDetails();
    })
  }
  goBack() {
    this._location.back();
  }
  rejectRefund() {
    const modalRef = this._modalService.open(RejectRefundComponent, { size: 'md' });
    modalRef.componentInstance.data = { ...this.details, collectionType: 7002 };
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        this.GetDirectPaymentSalesOrdersDetails();
      }
    })
  }
  downloadInvoices(invoices) {
    this._commonService.downloadInvoices({ invoices })
  }
  returnAction(details) {
    let action = details?.actionObj[0]?.lookupName.toLowerCase();
    if (action === 'view') {
      const modalRef = this._modalService.open(RefundDetailsComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details, orderType: 7002 };
    }
    else if (action === 'collect') {
      const modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
      modalRef.componentInstance.detailsItem = { ...details, isFromReturn: true };
      modalRef.componentInstance.collectionType = 7002;
      modalRef.componentInstance.sendtoLoadData.subscribe(x => {
        modalRef.dismiss();
        this.GetDirectPaymentSalesOrdersDetails();
      });
    }
    else if (action === 'sign') {
      const modalRef = this._modalService.open(SignComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details, orderType: 7002 };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.GetDirectPaymentSalesOrdersDetails();
        }
      });
    }
    else if (action === 'refund') {
      const modalRef = this._modalService.open(ModalPopupComponent, { size: 'lg' });
      modalRef.componentInstance.data = { returnType: '', details, orderType: 7002 };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.GetDirectPaymentSalesOrdersDetails();
        }
      });
    }
  }

  GetDirectPaymentSalesOrdersDetails() {
    return this.collectService
      .GetDirectPaymentSalesOrdersDetails(this.orderId)
      .subscribe((response: any) => {
        if (response) {
          this.details = response.data;
          console.log(this.details);
          this.orderStatus = (this.details?.order?.status[0]?.statusName).trim();
          const customername = this.details?.customer.customerName.split(' ');
          let firstChar = customername[0];
          let secChar = customername[1];
          this.custmerName = `${firstChar?.charAt(0)} ${secChar ? (secChar?.charAt(0)) : ''}`;
        }
      });
  }
}
