import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CollectService } from '../../collect/services/collect.service';
import { Location } from '@angular/common';
import { ModalPopupComponent } from '../components/modal-popup/modal-popup.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { RefundDetailsComponent } from '../components/refund-details/refund-details.component';
import { PaymentModalComponent } from '../../collect/payment-modal/payment-modal.component';
import { SignComponent } from '../components/sign/sign.component';
import { CommonService } from '../../../../../services/common.service';
import { CollectComponent } from '../components/collect/collect.component';
import { RejectRefundComponent } from '../components/reject-refund/reject-refund.component';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';

@Component({
  selector: 'app-spart-part-details',
  templateUrl: './spart-part-details.component.html',
  styleUrls: ['./spart-part-details.component.scss']
})
export class SpartPartDetailsComponent {
  details;
  spareParts = [];
  totalRecordCount = 0;
  orderId;
  orderStatus: string;
  constructor(
    private _collectService: CollectService,
    private _commonService: CommonService,
    private _activatedRoute: ActivatedRoute, private _location: Location, private _modalService: NgbModal) {
  }
  ngOnInit() {
    this._activatedRoute.queryParams.subscribe(params => {
      this.orderId = params['id'];
      this.getReturnOrderRefundDetails(this.orderId);
    })
  }
  goBack() {
    this._location.back();
  }
  getReturnOrderRefundDetails(id) {
    this._collectService.getSparePartsReturnOrdersPrtsV2(id).subscribe(response => {
      this.spareParts = response['data'];
      this.getSparePartsReturnOrders(id);
    })
  }
  getSparePartsReturnOrders(id) {
    let defaultParams = `orderNo=${id}`
    this._collectService.getSparePartsReturnOrders(defaultParams).subscribe((response: any) => {
      this.details = response?.data[0];
      this.orderStatus = (this.details?.statusObj[0]?.lookupName).trim();
    })
  }
  downloadInvoices() {
    //this._commonService.downloadInvoices({ invoices })
  }
  getInvoice() {
    const formData = new FormData();
    formData.append('returnOrderNo', this.orderId);
    this._collectService.viewReturnOrderOrigionalInvoice(formData).subscribe({
      next: response => {
        if (response.isSuccess) {
          this._commonService.downloadURI(response.data);
        }
      },
      error: err => {
        this.responseModal('error', err?.error.errors || err?.error.Errors);
      }
    })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    if (type == 'success')
      ref.componentInstance.message = message;
    ref.componentInstance.messageError = message;
    ref.componentInstance.type = type;
  }
  rejectRefund() {
    const modalRef = this._modalService.open(RejectRefundComponent, { size: 'md' });
    modalRef.componentInstance.data = { ...this.details, collectionType: 7003 };
    modalRef.componentInstance.eventData.subscribe(x => {
      if (x) {
        this.getReturnOrderRefundDetails(this.orderId);
      }
    })
  }
  returnAction(details) {
    let action = details?.actionObj[0]?.lookupName.toLowerCase();
    if (action === 'view') {
      const modalRef = this._modalService.open(RefundDetailsComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details };
    }
    else if (action === 'collect') {
      if (details?.grandAmount > 0) {
        const modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
        modalRef.componentInstance.detailsItem = { ...details, isFromReturn: true };
        modalRef.componentInstance.collectionType = 7003;
        modalRef.componentInstance.sendtoLoadData.subscribe(x => {
          modalRef.dismiss();
          this.getReturnOrderRefundDetails(this.orderId);
        });
      }
      else {
        const modalRef = this._modalService.open(CollectComponent, { size: 'lg' });
        modalRef.componentInstance.data = { details };
        modalRef.componentInstance.eventData.subscribe(x => {
          if (x?.isSuccess) {
            modalRef.dismiss();
            this.getReturnOrderRefundDetails(this.orderId);
          }
        });
      }
    }
    else if (action === 'sign') {
      const modalRef = this._modalService.open(SignComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getReturnOrderRefundDetails(this.orderId);
        }
      });
    }
    else if (action === 'refund') {
      const modalRef = this._modalService.open(ModalPopupComponent, { size: 'lg' });
      modalRef.componentInstance.data = { details };
      modalRef.componentInstance.eventData.subscribe(res => {
        if (res?.isSuccess) {
          modalRef.dismiss();
          this.getReturnOrderRefundDetails(this.orderId);
        }
      });
    }
  }
}
